import { c as create_ssr_component, e as escape } from "../../chunks/ssr.js";
import { io } from "socket.io-client";
const socket = io("https://share-clam-70.deno.dev/", {
  transports: ["websocket"]
});
const Page = create_ssr_component(($$result, $$props, $$bindings, slots) => {
  let chatid;
  socket.on("connect", () => {
    console.log(`Connected to ${socket.id}`);
    chatid = socket.id;
  });
  socket.on("chatMsg", (data) => {
    let newMsg = document.createElement("li");
    newMsg.classList.add("text-neutral-600", "text-lg", "bg-gray-300", "rounded-lg");
    newMsg.textContent = `${data}`;
  });
  chatid = "...";
  return `<main class="bg-gray-800 h-dvh w-full flex justify-center items-center"><h1 class="absolute text-gray-300 top-1">Your chat id is : ${escape(chatid)}</h1> <div class="relative flex flex-col overflow-y-auto min-w-[70%] max-w-[85%] bg-gray-900 h-[75%] text-stone-500 rounded-xl"><ul id="chat-box" class="my-4 break-words"></ul> <input type="text" class="absolute bottom-1 w-[90%] self-center bg-zinc-600 text-stone-300 p-3 rounded-lg"></div></main>`;
});
export {
  Page as default
};
